/* my style */
.button {
  font-weight: 500;
  font-size: 16px;
  line-height: 39px;
  padding: 10px 22px;
  background-color: #9b8a60;
  transition: 0.4s;
  color: #ffffff !important;
  text-decoration: none !important;
  font-style: normal !important;
  white-space: nowrap;
  cursor: pointer;
}
.button--big {
  text-transform: uppercase;
  margin-left: 50%;
  transform: translateX(-50%);
}
.button::after {
  display: none;
}
@media screen and (max-width: 767px) {
  .button {
    font-size: 14px;
    line-height: 25px;
  }
}
@media screen and (min-width: 1025px) {
  .button:hover {
    background-color: #847348;
  }
}
.button-prev {
  position: relative;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #f0f0ed;
  transition: 0.3s;
  font-family: "Calibri", sans-serif;
}
@media screen and (min-width: 1025px) {
  .button-prev:hover {
    color: #9b8a60;
  }
  .button-prev:hover svg {
    fill: #9b8a60;
  }
}
.button-prev-img {
  border: 1px solid rgba(255, 255, 255, 0.13);
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.button-prev span {
  margin-left: 8px;
  font-size: 15px;
  line-height: 18px;
  color: inherit;
}
.button-prev:visited {
  color: inherit;
}
.button-prev__icon-arrow-prev {
  fill: #ffffff;
  width: 22px;
  height: 15px;
  transition: 0.3s;
}
.button--white {
  background: #F2F2F2;
  color: #1A1919 !important;
}
@media screen and (min-width: 1025px) {
  .button--white:hover {
    background-color: #1A1919;
    color: #ffffff !important;
  }
}

.tag {
  padding: 10px 19px;
  background: #262525;
  border-radius: 1000px;
  color: #ffffff;
  margin-right: 8px;
  margin-bottom: 11px;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  cursor: pointer;
  transition: 0.4s;
  user-select: none;
  display: block;
}
.tag--selected {
  background: #9b8a60;
}
@media screen and (min-width: 1025px) {
  .tag:hover {
    background-color: #9b8a60;
  }
}

.img-popup {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  background-color: #000000;
  z-index: 9999;
  transition: 0.4s;
}
.img-popup.active {
  opacity: 1;
  visibility: visible;
}
.img-popup__button-prev {
  position: absolute;
  top: 0;
  left: -30%;
}
@media only screen and (max-width: 1200px) {
  .img-popup__button-prev {
    left: -140px;
  }
}
@media only screen and (max-width: 993px) {
  .img-popup__button-prev {
    left: 0;
    top: -44px;
    width: 35px;
    height: 35px;
  }
  .img-popup__button-prev .button-prev-img {
    width: 35px;
    height: 35px;
    flex-shrink: 0;
  }
  .img-popup__button-prev .button-prev-img svg {
    width: 19px;
    height: 12px;
  }
}
.img-popup__close {
  position: absolute;
  top: 0;
  right: -75px;
  cursor: pointer;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.16);
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media only screen and (max-width: 993px) {
  .img-popup__close {
    right: 0;
    top: -45px;
    width: 35px;
    height: 35px;
  }
}
.img-popup__close svg {
  width: 29px;
  height: 29px;
  fill: #fff;
  transition: 0.3s;
}
@media only screen and (max-width: 550px) {
  .img-popup__close svg {
    width: 21px;
    height: 21px;
  }
}
@media screen and (min-width: 1025px) {
  .img-popup__close:hover svg {
    fill: #9b8a60;
  }
}
@media screen and (max-height: 500px) {
  .img-popup--transparent {
    padding-bottom: 30px;
    padding-top: 30px;
    align-items: flex-end;
  }
}
.img-popup--transparent .button-prev {
  display: none;
}
.img-popup--transparent .img-popup__close {
  right: 0;
  top: 0;
}
@media only screen and (max-width: 993px) {
  .img-popup--transparent .img-popup__close {
    top: -55px;
    right: 0;
  }
}
@media screen and (max-height: 500px) {
  .img-popup--transparent .img-popup__close {
    right: -35px;
    top: -35px;
  }
}
.img-popup--transparent .img-popup__dialog {
  background-color: transparent;
  width: calc(100% - 90px);
  height: calc(100% - 90px);
  padding: 60px;
  display: flex;
  align-items: center;
}
@media only screen and (max-width: 993px) {
  .img-popup--transparent .img-popup__dialog {
    padding: 0;
    width: calc(100% - 60px);
    height: calc(100% - 150px);
  }
}
@media screen and (max-height: 500px) {
  .img-popup--transparent .img-popup__dialog {
    width: calc(100% - 200px);
    height: calc(100% - 15px);
  }
}
.img-popup--transparent .img-popup__dialog img {
  object-fit: cover;
  height: 100%;
  width: 100%;
  max-height: 100%;
}
.img-popup--transparent .img-popup__dialog::after {
  display: none;
}
.img-popup__dialog {
  position: relative;
  width: 50%;
  height: auto;
  padding: 25px 38px;
  border-radius: 0 0 0 12px;
  color: #1a1919;
  background-color: #fff;
}
