.header__nav-list-sub-menu {
  position: absolute;
  display: flex;
  flex-direction: column;
  list-style: none;
  width: auto;
  top: 100%;
  font-size: 15px;
  line-height: 180%;
  color: #1a1919;
  padding: 40px 0 0 0;
  transform-origin: top;
  transform: rotateX(-90deg);
}
@media only screen and (min-width: 994px) {
