    color: #f0f0ed;
  }
  .header__nav-list-sub-menu.active {
    display: block;
  }
}
.header__nav-list-sub-menu::before {
  content: "";
  position: absolute;
  width: 100%;
  height: calc(100% - 40px);
  background-color: #f7f7f7;
  border-radius: 3px;
  z-index: -1;
