  transition: 0.4s;
}
@media screen and (min-width: 1025px) {
  .header__nav-list-sub-menu li a:hover {
    color: #9b8a60;
  }
}
@media only screen and (max-width: 993px) {
  .header__nav-list-sub-menu li {
