.sh-burger {
  position: relative;
  display: none;
  width: 40px;
  height: 40px;
  cursor: pointer;
  transition: 0.2s;
  z-index: 100;
}
@media only screen and (max-width: 993px) {
  .sh-burger {
    display: flex;
    margin-left: 40px;
  }
}
.sh-burger span,
.sh-burger span::before {
  position: absolute;
  right: 0;
  margin-top: -1px;
  margin-left: -17px;
  background-color: #fff;
  transition: 0.2s;
}
.sh-burger span {
  width: 15px;
  height: 4px;
  bottom: 13px;
  right: 10px;
}
.sh-burger span::before {
  content: "";
  bottom: 9px;
  transition: 0.2s;
  width: 19px;
  height: 4px;
}
.sh-burger--active span {
  width: 19px;
  transform: rotate(-45deg);
  top: 50%;
}
.sh-burger--active span::before {
  transform: rotate(90deg);
  bottom: 0;
}

.langs {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: auto;
  right: 50%;
  top: 100%;
  font-size: 15px;
  line-height: 180%;
  color: #1a1919;
  padding: 40px 0 0 0;
  transform-origin: top;
  transform: rotateX(-90deg) translateX(50%);
  transition-property: transform;
  transition-duration: 0.4s;
}
@media only screen and (max-width: 993px) {
  .langs {
    padding: 0;
  }
}
.langs.active {
  transform: rotateX(0) translateX(50%);
}
.langs::before {
  content: "";
  position: absolute;
  width: 100%;
  height: calc(100% - 40px);
  background-color: #f7f7f7;
  border-radius: 3px;
  z-index: -1;
}
@media only screen and (max-width: 993px) {
  .langs::before {
    height: 100%;
  }
}
.langs .sh-lang-switcher {
  color: inherit;
  position: relative;
  transition: 0.4s;
  padding: 0 18px;
}
.langs .sh-lang-switcher a {
  font-family: Calibri, sans-serif;
  display: flex;
  text-decoration: none;
  color: inherit;
  white-space: nowrap;
  transition: color 0.4s;
}
@media screen and (min-width: 1025px) {
  .langs .sh-lang-switcher a:hover {
    color: #9b8a60;
  }
}
.langs .sh-lang-switcher:first-child {
  padding-top: 16px;
}
.langs .sh-lang-switcher:last-child {
  padding-bottom: 16px;
}

