.p-sheme {
  width: 100%;
  position: relative;
  max-width: 862px;
  margin-top: 210px;
  margin-bottom: 150px;
}
@media only screen and (max-width: 993px) {
  .p-sheme {
    margin-bottom: 33px;
  }
}
.p-sheme__img {
  width: 100%;
  display: block;
}
.p-sheme__img img {
  display: block;
  width: 100%;
  height: auto;
}
.p-sheme__dots {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
}
.p-sheme__dot {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.p-sheme__dot-point {
  width: 22px;
  height: 22px;
  background-color: #9b8a60;
  border-radius: 50%;
  cursor: pointer;
  transition: transform 0.1s;
  position: relative;
}
.p-sheme__dot-point::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #fff;
  pointer-events: none;
}
.p-sheme__dot-point::before {
  content: "";
  position: absolute;
  box-sizing: border-box;
  width: 30px;
  height: 30px;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  border-radius: 50%;
  border: 1px solid rgba(155, 138, 96, 0.32);
  opacity: 0;
  pointer-events: none;
}
.p-sheme__dot-line {
  width: 1px;
  display: block;
  height: 209px;
  position: absolute;
  left: 11px;
  bottom: 50px;
  user-select: none;
  pointer-events: none;
}
.p-sheme__dot-line::before {
  content: "";
  background: #9b8a60;
  height: 0;
  width: 100%;
  display: block;
  transition: height 0.2s;
  position: absolute;
  bottom: 0;
  left: 0;
}
.p-sheme__dot-text {
  width: 180px;
  display: flex;
  position: absolute;
  padding-left: 15px;
  font-size: 15px;
  line-height: 21px;
  transition: 0.3s;
  top: -25px;
  left: -2px;
  opacity: 0;
  user-select: none;
  pointer-events: none;
}
.p-sheme__dot-text::before {
  content: "";
  position: absolute;
  left: 0;
  top: 8px;
  width: 6px;
  height: 6px;
  background-color: #9b8a60;
  border-radius: 50%;
  transition: opacity 0.2s;
}
.p-sheme__dot.active .p-sheme__dot-point {
  transform: scale(2);
}
.p-sheme__dot.active .p-sheme__dot-point::before {
  opacity: 1;
}
.p-sheme__dot.active .p-sheme__dot-line::before {
  height: 100%;
}
.p-sheme__dot.active .p-sheme__dot-text {
  animation: popup 0.2s linear 0.2s forwards;
}

